/* src/App.css */

* {
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  line-height: 1.6;
  background: linear-gradient(to bottom, #78e2ff 0%, #ff99cc 100%);
}

/* Keep the header background as an image */
header {
  background: url('https://images.unsplash.com/photo-1451187580459-43490279c0fa?auto=format&fit=crop&q=80') no-repeat center center / cover;
  color: #fff;
  text-align:start;
  
  padding: 250px;
}

header h1 strong{
 color:rgb(254, 217, 122)

}

.hero h1 {
  font-size: 3em;
  margin: 0;
}

.hero p {
  font-size: 1.2em;
}

.btn {
  display: inline-block;
  padding: 10px 15px;
  background: linear-gradient(to right, #78e2ff 0%, #ff99cc 100%);
  color: rgb(0, 0, 0);
  text-decoration:double;
  border-radius: 5px;
  transition: 0.3s;
}
/* .btn:hover {
  background: #000000;
} */
.top-nav {
  color: #ffffff;
  justify-content:space-between;
  background-color: #080808;
}

nav ul {
  
  list-style: none;
  padding: 0px;
  margin: 0;
  display: flex;
  justify-content: flex-end;
}

nav a {
  color: #fff;
  text-decoration: none;
  padding: 5px 10px;
  transition: color 0.3s;
}

nav a:hover {
  color: #007bff;
}

section {
  padding: 20px;
  max-width: 800px;
  margin: auto;
}

