#about h2, #projects h2, #education h2, #resume h2, #contact h2 {
  text-align: center;
  color: #080808;
}

.project {
  
  border: 1px solid #000000;
  border-radius: 5px;
  padding: 15px;
  margin: 10px 0;
  transition: transform 0.2s;
}

.project:hover {
  transform: scale(1.02);
}

footer {
  text-align: center;
  color: rgb(0, 0, 0);
}

.social-icons a {
  margin: 0 10px;
  color: #000000;
  text-decoration: none;
  transition: color 0.3s;
}

.social-icons a:hover {
  color: #007bff;
}




/* src/App.css */

.skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.skill-item {
  text-align: center;
  margin: 20px;
  transition: transform 0.2s;
}

.skill-item img {
  width:50px;
  height: 50px; 
}

.skill-item p {
  margin-top: 10px;
  font-size: 1.2em;
  color: #333;
}

.skill-item:hover {
  transform: scale(1.1); 
}


.resume-navigation {
 justify-content: space-evenly;
  padding: 10px;
  border-radius: 5px; 
  margin-bottom: 20px; 
  text-align: center; 
  border: 1px solid #000000;
}

.resume-navigation ul {
  list-style-type: none; 
  padding: 0; 
}

.resume-navigation li {
  display: inline;
  margin: 0 15px; 
}

.resume-navigation a {
  text-decoration: none; 
  color: #007bff; 
  font-weight: bold; 
}

.resume-navigation a:hover {
  text-decoration: underline; 
}

.education-section, .achievements-section {
  margin: 20px 0;
}

.education-item, .achievement-item {
  display: grid;
  grid-template-columns: 2fr 2fr 1fr;
  align-items: center; 
  border: 1px solid #000000; 
  border-radius: 5px;
  padding: 15px;
  
  margin: 10px 0; /* Spacing between items */

}

.educational-details {
  grid-column: 1; /* Span across the first column */
}

.education-place {
  grid-column: 2; /* Span across the second column */
}

.education-year, .education-percentage {
  grid-column: 3; /* Span across the third column */
  text-align: right; /* Align text to the right */
}

.education-item h4 {
  margin: 0;
  font-size: 1.5em; /* Degree title size */
  color: #000000; /* Darker text color */
}

.education-item p, .achievement-item p {
  margin: 5px 0; /* Spacing between lines */
  color: #555; /* Medium grey for other text */
}

h2, h3 {
  color: #444; /* Heading color */
  text-align: center; /* Center align headings */
}